import React from 'react'

import { FDriverFragment, useQFilterDrivers } from '../../graph/generated'
import { ARRAY_EMPTY } from '../../helpers/form'
import { formatFullName } from '../../helpers/formats'
import { i18n } from '../../i18n'
import { FormDropdownMulti } from '../forms/FormDropdownMulti'
import { useBranchesFilter } from './BranchesFilter'

type TDriver = FDriverFragment

export const DriversFilter = React.memo<NoChildren>(() => {
  const branches = useBranchesFilter()
  const { getStash, loading } = useQFilterDrivers({
    branches,
  })
  return (
    <FormDropdownMulti<TDriver>
      fieldName="drivers"
      isLoading={loading}
      options={getStash(data => data.drivers) || ARRAY_EMPTY}
      placeholder={i18n.t`Filter by drivers`}
      formatOptionLabel={formatFullName}
      getOptionFilter={formatFullName}
    />
  )
})
