import { Trans } from '@lingui/macro'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { Sizer } from '../../../atoms/Sizer'
import { useMTransferOrder } from '../../../graph/generated'
import { useCanMutate } from '../../../hooks/useAccessGuard'
import { CompanyBranchDropdown } from '../../../molecules/CompanyBranchDropdown'
import { SelectButton } from '../../../molecules/SelectButton'

type TProps = NoChildren & {
  orderId: ID
}

export const OrderTransferToBranch: React.FC<TProps> = ({ orderId }) => {
  const { user } = useRoot()
  const [transferOrder] = useMTransferOrder()
  const canTransferOrder = useCanMutate('transferOrder')
  const [selectedBranch, setSelectedBranch] = React.useState<Maybe<ID>>(null)

  const onTransferOrder = () => {
    if (selectedBranch) {
      transferOrder({ orderId, branchId: selectedBranch })
    }
  }

  if (!canTransferOrder) {
    return null
  }

  return (
    <SelectButton
      confirmNode={<Trans>Transfer</Trans>}
      onConfirm={onTransferOrder}
      selectNode={
        <Sizer width={14}>
          <CompanyBranchDropdown
            branches={user.branches}
            onSingleSelection={branch => branch && setSelectedBranch(branch.id)}
          />
        </Sizer>
      }
    >
      <Trans>Transfer to branch</Trans>
    </SelectButton>
  )
}
