import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@material-ui/core'
import { Observer, useLocalStore } from 'mobx-react-lite'
import React from 'react'

type TRenderArea = ReactNodes | ((api: IDialogApi) => ReactNodes)

interface IProps {
  initialOpen?: boolean
  title?: string | ReactNode
  actions?: TRenderArea
  content: TRenderArea
}

interface IDialogApi {
  isOpen: boolean
  open: () => void
  close: () => void
}

export function useSmartDialog({
  content,
  title,
  actions,
  initialOpen = false,
}: IProps) {
  const state = useLocalStore(() => ({
    isOpen: initialOpen,
    open() {
      this.isOpen = true
    },
    close() {
      this.isOpen = false
    },
  }))

  const renderArea = (area: TRenderArea) => {
    if (typeof area === 'function') {
      return area(state)
    }
    return area
  }

  const dialog = (
    <Observer>
      {() => (
        <Dialog open={state.isOpen} onClose={state.close}>
          {title && <DialogTitle>{title}</DialogTitle>}
          <DialogContent>{renderArea(content)}</DialogContent>
          {actions && <DialogActions>{renderArea(actions)}</DialogActions>}
        </Dialog>
      )}
    </Observer>
  )

  return [dialog, state] as [typeof dialog, typeof state]
}
