import React from 'react'
import styled from 'styled-components'

import { formatPrice } from '../../../helpers/formats'
import { Colors } from '../../../styling'

interface IProps {
  label: string
  price: number
  highlight?: boolean
}

const OrderWareLineWrap = styled.div`
  display: flex;
  justify-content: space-between;
  padding-left: 3rem;
`

const NameContainerStyled = styled.div`
  flex: 1 0;
  color: ${Colors.greyMiddle};
`
const PriceContainerStyled = styled.div`
  flex: 0 0 4.5rem;
  text-align: right;
  color: ${Colors.greyMiddle};
`

export const OrderWarePriceLine: React.FC<IProps> = ({
  label,
  price,
  highlight = false,
}) => (
  <OrderWareLineWrap>
    <NameContainerStyled>{label}</NameContainerStyled>
    <PriceContainerStyled>
      {highlight ? <b>{formatPrice(price)}</b> : formatPrice(price)}
    </PriceContainerStyled>
  </OrderWareLineWrap>
)
