import { Trans } from '@lingui/macro'
import React from 'react'

import { DeliveryMinutes } from '../../../atoms/DeliveryMinutes'
import { DeliveryTypeEnum } from '../../../graph/generated'
import { useSmartDialog } from '../../../hooks/useSmartDialog'
import { TouchButton } from '../../../molecules/TouchButton'

interface IOptions {
  title: string
  deliveryType: DeliveryTypeEnum
  onConfirm(minutes: number, onSuccess: () => void): Promise<unknown>
}

export function useTimeSelectionDialog({
  title,
  deliveryType,
  onConfirm,
}: IOptions) {
  const [isSubmitting, setSubmitting] = React.useState(false)
  return useSmartDialog({
    title,
    content: ({ close }) => (
      <DeliveryMinutes
        disabled={isSubmitting}
        deliveryType={deliveryType}
        onMinuteClick={minutes => {
          setSubmitting(true)
          onConfirm(minutes, close).finally(() => {
            setSubmitting(false)
          })
        }}
      />
    ),
    actions: ({ close }) => (
      <TouchButton disabled={isSubmitting} onClick={close}>
        <Trans>Cancel</Trans>
      </TouchButton>
    ),
  })
}
