import React from 'react'

import { SelectControl } from '../../../atoms/SelectControl'
import { FPaymentMethodFragment as FPaymentMethod } from '../../../graph/generated'
import { useDropdownSingle } from '../../../hooks/useDropdownSingle'
import { idProp } from '../../../tools/ramda'

type TProps = NoChildren & {
  paymentMethods: RoA<FPaymentMethod>
  selectedMethod: MaybeID
  onSelection: (method: MaybeID) => void
  inputStyles?: React.CSSProperties
}

export const PaymentMethodDropdown: React.FC<TProps> = ({
  paymentMethods,
  selectedMethod,
  onSelection,
  inputStyles = {},
  ...props
}) => {
  const selectProps = {
    options: paymentMethods,
    getOptionValue: idProp,
    formatOptionLabel: (method: FPaymentMethod) =>
      method.description || method.paymentGate.code,
    ...props,
  }

  const dropdownProps = useDropdownSingle<FPaymentMethod>({
    ...selectProps,
    selected: selectedMethod,
    onSelectionChange: onSelection,
  })

  return (
    <SelectControl<FPaymentMethod>
      styles={{
        control: base => ({
          ...base,
          height: '100%',
          margin: 0,
          ...inputStyles,
        }),
      }}
      {...dropdownProps}
      {...selectProps}
    />
  )
}
