import React from 'react'

import {
  QFilterCashboxesCashBoxes,
  useQFilterCashboxes,
} from '../../graph/generated'
import { ARRAY_EMPTY } from '../../helpers/form'
import { i18n } from '../../i18n'
import { FormDropdownMulti } from '../forms/FormDropdownMulti'
import { useBranchesFilter } from './BranchesFilter'

type TCashbox = QFilterCashboxesCashBoxes

export const CashboxesFilter = React.memo<NoChildren>(() => {
  const branches = useBranchesFilter()
  const { getStash, loading } = useQFilterCashboxes({
    branches,
  })
  return (
    <FormDropdownMulti<TCashbox>
      fieldName="cashboxes"
      isLoading={loading}
      options={getStash(data => data.cashBoxes) || ARRAY_EMPTY}
      placeholder={i18n.t`Filter by cashbox`}
      formatOptionLabel={formatCashboxName}
      getOptionFilter={formatCashboxName}
    />
  )
})

function formatCashboxName({ name, uuid }: TCashbox) {
  return name || uuid
}
