import { Trans } from '@lingui/macro'
import React from 'react'

import { Headline } from '../../../atoms/Headline'
import { MarginBox } from '../../../atoms/MarginBox'
import { MonospaceOutput } from '../../../atoms/MonospaceOutput'
import { useQStatsSales } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { useStatsFilterCurrent } from '../hooks/useStatsFilter'

const StatsDisplay: React.FC<{ plain: string }> = ({ plain, children }) => {
  return (
    <>
      <MarginBox margin="1.5rem 0 0 0">
        <Headline light={true}>{children}</Headline>
      </MarginBox>
      <MonospaceOutput text={plain} scrollable={true} />
    </>
  )
}

export const SalesStatisticsContent: React.FC = () => {
  const filter = useStatsFilterCurrent()
  const { data, loading } = useQStatsSales(
    { filter: filter! },
    { skip: !filter },
  )

  if (!filter) {
    return null
  }

  if (loading) {
    return <MonospaceOutput text={i18n.t`Computing`} />
  }

  const sales = data && data.sales
  if (!sales) {
    return null
  }

  return (
    <>
      <StatsDisplay plain={sales.daily}>
        <Trans>Daily statistics</Trans>
      </StatsDisplay>

      <StatsDisplay plain={sales.vat}>
        <Trans>VAT overview</Trans>
      </StatsDisplay>

      <StatsDisplay plain={sales.category}>
        <Trans>By category</Trans>
      </StatsDisplay>

      <StatsDisplay plain={sales.delivery}>
        <Trans>By type</Trans>
      </StatsDisplay>

      <StatsDisplay plain={sales.drivers}>
        <Trans>By driver</Trans>
      </StatsDisplay>
    </>
  )
}
