import { XFormRender } from '@speedlo/xform'
import React from 'react'

import { StatsFilterProvider, useStatsFilter } from '../hooks/useStatsFilter'

export const SalesStatisticsLoader: React.FC<Required<SomeChildren>> = ({
  children,
}) => {
  const { filter, xform } = useStatsFilter(
    'cashbox',
    'cashier',
    'sector',
    'origin',
    'occasion',
  )
  return (
    <XFormRender xform={xform}>
      <StatsFilterProvider value={filter}>{children}</StatsFilterProvider>
    </XFormRender>
  )
}
