import { useNow } from '@speedlo/hooks'
import { XForm } from '@speedlo/xform'
import { subDays } from 'date-fns'
import React from 'react'

import { useBranchFilter } from '../../../components/filters/BranchFilter'
import {
  QClosureHistoryHistory,
  useLazyQClosureHistory,
} from '../../../graph/generated'

export type TReport = QClosureHistoryHistory

type TClosureHistoryCtx = {
  history?: RoA<TReport>
  loading: boolean
  called: boolean
}

const context = React.createContext<TClosureHistoryCtx>({
  called: false,
  loading: false,
})

export const useClosureHistory = () => React.useContext(context)!

export const ClosureHistoryLoader: React.FC<Required<SomeChildren>> = ({
  children,
}) => {
  const [execute, result] = useLazyQClosureHistory()
  const getNow = useNow()

  const ctx: TClosureHistoryCtx = {
    loading: result.loading,
    called: result.called,
    history: result.data && result.data.history,
  }

  const branch = useBranchFilter(false)

  return (
    <XForm
      onSubmit={(formValues: { dateFrom: Date; dateTo: Date }) => {
        if (branch) {
          execute({
            branchId: branch,
            dateFrom: formValues.dateFrom,
            dateTo: formValues.dateTo,
          })
        }
      }}
      initialValues={{ dateFrom: subDays(getNow(), 1), dateTo: getNow() }}
    >
      <context.Provider value={ctx}>{children}</context.Provider>
    </XForm>
  )
}
