import { observer } from 'mobx-react-lite'
import React from 'react'

import { MonospaceOutput } from '../../../atoms/MonospaceOutput'
import { useBranchFilter } from '../../../components/filters/BranchFilter'
import { useQClosureReport } from '../../../graph/generated'
import { i18n } from '../../../i18n'

interface IProps {
  reportId?: string
}

export const ClosureStatisticsContent = observer<IProps>(
  ({ reportId = '' }) => {
    const branch = useBranchFilter()
    const skip = !branch
    const { data, error, loading } = useQClosureReport(
      {
        branchId: branch!,
        reportId,
        showReport: Boolean(reportId),
      },
      { skip },
    )

    if (skip) {
      return <MonospaceOutput text={i18n.t`Select a branch`} />
    }

    if (loading) {
      return <MonospaceOutput text={i18n.t`Computing`} />
    }

    if (error || !data) {
      return null
    }

    return (
      <MonospaceOutput
        scrollable={true}
        text={
          reportId && data.report
            ? data.report.plainData
            : data.lastReport.plainData
        }
      />
    )
  },
)
