import React from 'react'

import {
  QFilterCountriesCountries,
  useQFilterCountries,
} from '../../graph/generated'
import { ARRAY_EMPTY } from '../../helpers/form'
import { i18n } from '../../i18n'
import { RM } from '../../tools/ramda'
import { FormDropdownSingle } from '../forms/FormDropdownSingle'

type TCountry = QFilterCountriesCountries

export const CountryFilter = React.memo<NoChildren>(() => {
  const { getStash, loading } = useQFilterCountries()

  return (
    <FormDropdownSingle<TCountry>
      fieldName="country"
      placeholder={i18n.t`Filter by country`}
      noOptionsMessage={() => i18n.t`No countries`}
      options={getStash(data => data.countries) || ARRAY_EMPTY}
      isLoading={loading}
      isClearable={true}
      getOptionLabel={RM.prop('code')}
      getOptionFilter={RM.prop('code')}
      getOptionValue={RM.prop('enum')}
    />
  )
})
