import { createStatePersistence } from '@speedlo/hooks'
import React from 'react'

import { SelectControl } from '../../atoms/SelectControl'
import { FCompanyBranchFragment } from '../../graph/generated'
import { formatBranchName } from '../../helpers/formats'
import { useDropdownFilter } from '../../hooks/useDropdownFilter'
import { useDropdownSingle } from '../../hooks/useDropdownSingle'
import { useQueryParams } from '../../hooks/useQueryParams'
import { useUserModel } from '../../hooks/useUserModel'
import { i18n } from '../../i18n'
import { idProp, idPropEq } from '../../tools/ramda'

export const useBranchFilterEditable = createStatePersistence<MaybeID>(
  'branch',
  null,
)

type TProps = NoChildren

export const BranchFilter: React.FC<TProps> = () => {
  const userModel = useUserModel()
  const [branch, setBranch] = useBranchFilterEditable()

  const { getQueryParam } = useQueryParams()
  const branchId = getQueryParam('branchId')

  React.useEffect(() => {
    if (branchId) {
      setBranch(branchId)
    }
  }, [setBranch, branchId])

  const selectProps = {
    options: userModel.branches,
    placeholder: i18n.t`Filter branch...`,
    formatOptionLabel: opt => formatBranchName(opt),
    hideOnSingleOption: true,
    getOptionValue: idProp,
  }

  const dropdownProps = useDropdownSingle<FCompanyBranchFragment>({
    ...selectProps,
    onSelectionChange: setBranch,
    selected: branch,
  })

  const filterProps = useDropdownFilter({
    getOptionFilter: formatBranchName,
  })

  return (
    <SelectControl<FCompanyBranchFragment>
      {...dropdownProps}
      {...filterProps}
      {...selectProps}
    />
  )
}

export function useBranchFilter(withUserDefaultBranchWhenEmpty = true): ID {
  const userModel = useUserModel()
  const [persistedBranch, setPersistedBranch] = useBranchFilterEditable()

  const hasPersistedBranch = persistedBranch !== null
  const hasAccessToPersistedBranch = Boolean(
    hasPersistedBranch && userModel.branches.find(idPropEq(persistedBranch)),
  )

  React.useEffect(() => {
    if (hasPersistedBranch && !hasAccessToPersistedBranch) {
      setPersistedBranch(null)
    }
  }, [hasAccessToPersistedBranch, hasPersistedBranch, setPersistedBranch])

  const noBranchSelected = !hasPersistedBranch || !hasAccessToPersistedBranch
  const userHasOnlyOneBranch = userModel.branches.length === 1

  if (
    userHasOnlyOneBranch ||
    (noBranchSelected && withUserDefaultBranchWhenEmpty)
  ) {
    return userModel.defaultBranch.id
  }
  // ? Not sure how to enforce return type correct to be always ID
  // @ts-ignore
  return persistedBranch
}
