import DateFnsUtils from '@date-io/date-fns'
import { MuiPickersUtilsProvider } from '@material-ui/pickers'
import React from 'react'

import { DFLocale } from '../tools/datefns'

export const DateTimeFormatProvider: React.FC<Required<SomeChildren>> = ({
  children,
}) => {
  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils} locale={DFLocale}>
      {children}
    </MuiPickersUtilsProvider>
  )
}
