import { Trans } from '@lingui/macro'
import React from 'react'

import {
  FPaymentMethodFragment as FPaymentMethod,
  useMChangePaymentMethod,
} from '../../../graph/generated'
import { useCanMutate } from '../../../hooks/useAccessGuard'
import { SelectButton } from '../../../molecules/SelectButton'
import { Radiuses } from '../../../styling'
import { RM } from '../../../tools/ramda'
import { PaymentMethodDropdown } from './PaymentMethodDropdown'

interface IProps {
  orderId: ID
  paymentMethods: RoA<FPaymentMethod>
}

export const OrderPaymentMethod: React.FC<IProps> = ({
  orderId,
  paymentMethods,
}) => {
  const hasSomeMethod = paymentMethods.length > 0
  const [changePaymentMethod] = useMChangePaymentMethod()
  const canChangePaymentMethod = useCanMutate('updateOrderPaymentMethod')
  const [selectedMethod, setSelectedMethod] = React.useState<MaybeID>(null)

  React.useEffect(() => {
    setSelectedMethod(() =>
      hasSomeMethod ? RM.head(paymentMethods)!.id : null,
    )
  }, [hasSomeMethod, paymentMethods])

  const onChangePaymentMethod = () => {
    if (selectedMethod) {
      changePaymentMethod({ orderId, paymentMethodId: selectedMethod })
    }
  }

  if (!canChangePaymentMethod || !hasSomeMethod) {
    return null
  }

  return (
    <SelectButton
      confirmNode={<Trans>Change</Trans>}
      onConfirm={onChangePaymentMethod}
      selectNode={
        <PaymentMethodDropdown
          paymentMethods={paymentMethods}
          selectedMethod={selectedMethod}
          onSelection={setSelectedMethod}
          inputStyles={{
            borderRadius: Radiuses.Button,
            borderTopRightRadius: 0,
            borderBottomRightRadius: 0,
          }}
        />
      }
    >
      <Trans>Change payment method</Trans>
    </SelectButton>
  )
}
