import { Trans } from '@lingui/macro'
import { Edit } from '@material-ui/icons'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRow } from '../../../atoms/FlexRow'
import { Headline } from '../../../atoms/Headline'
import { Loading } from '../../../atoms/Loading'
import { RelativeLink } from '../../../atoms/RelativeLink'
import { SizerButton } from '../../../atoms/SizerButton'
import { useBranchFilter } from '../../../components/filters/BranchFilter'
import { formatFullDateTime } from '../../../helpers/formats'
import { TouchButton } from '../../../molecules/TouchButton'
import { useClosureHistory } from '../organisms/ClosureHistoryLoader'

export const ClosureHistoryTable = observer(() => {
  const { history, loading, called } = useClosureHistory()
  const branch = useBranchFilter(false)
  if (!called) {
    return null
  }
  if (loading) {
    return (
      <Headline>
        <Loading />
      </Headline>
    )
  }
  if (!branch) {
    return (
      <Headline>
        <Trans>Select a branch</Trans>
      </Headline>
    )
  }
  if (!history || history.length === 0) {
    return (
      <Headline>
        <Trans>No closures found</Trans>
      </Headline>
    )
  }
  return (
    <>
      {history.map(report => (
        <FlexRow key={report.id}>
          <FlexColumn fixedWidth={true}>
            <RelativeLink to={`../${report.id}`}>
              <SizerButton>
                <TouchButton icon={Edit} />
              </SizerButton>
            </RelativeLink>
          </FlexColumn>
          <FlexColumn widthGrowIndex={1}>
            {`[${report.reportSequence}] ${formatFullDateTime(
              report.reportDatetime,
            )}`}
          </FlexColumn>
        </FlexRow>
      ))}
    </>
  )
})
