import { Schedule } from '@material-ui/icons'
import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import {
  DeliveryTypeEnum,
  useMOrderTimeNotification,
} from '../../../graph/generated'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { useTimeSelectionDialog } from '../hooks/useTimeSelectionDialog'

interface IProps {
  orderId: ID
  isDelivery: boolean
}

export const OrderStateTimeNotification: React.FC<IProps> = ({
  orderId,
  isDelivery,
}) => {
  const [sendTimeNotification] = useMOrderTimeNotification()
  const { enqueueSuccess } = useSnackBar()

  const [dialog, { open }] = useTimeSelectionDialog({
    deliveryType: isDelivery
      ? DeliveryTypeEnum.Messenger
      : DeliveryTypeEnum.Pickup,
    title: isDelivery
      ? i18n.t`Send expected time of delivery to customer (in minutes)`
      : i18n.t`Send expected time of pickup to customer (in minutes)`,
    async onConfirm(minutes, onSuccess) {
      const result = await sendTimeNotification({ orderId, minutes })
      if (result) {
        onSuccess()
        enqueueSuccess(
          isDelivery
            ? i18n.t`Expected time of delivery was set`
            : i18n.t`Expected time of pickup was set`,
        )
      }
    },
  })

  return (
    <CanMutate mutationName="orderNotifyCustomer">
      <TouchButton icon={Schedule} onClick={open} />
      {dialog}
    </CanMutate>
  )
}
