import React from 'react'

import { QFilterOriginsOrigins, useQFilterOrigins } from '../../graph/generated'
import { ARRAY_EMPTY } from '../../helpers/form'
import { i18n } from '../../i18n'
import { RM } from '../../tools/ramda'
import { FormDropdownMulti } from '../forms/FormDropdownMulti'

type TOrigin = QFilterOriginsOrigins

export const OriginsFilter = React.memo<NoChildren>(() => {
  const { getStash, loading } = useQFilterOrigins()

  return (
    <FormDropdownMulti<TOrigin>
      fieldName="origins"
      placeholder={i18n.t`Filter by order origins`}
      noOptionsMessage={() => i18n.t`No origins`}
      options={getStash(data => data.origins) || ARRAY_EMPTY}
      isLoading={loading}
      getOptionLabel={RM.prop('code')}
      getOptionFilter={RM.prop('code')}
      getOptionValue={RM.prop('enum')}
    />
  )
})
