import { Plural, Trans } from '@lingui/react'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { useBranchesFilter } from '../../../components/filters/BranchesFilter'
import { FormInputDateTime } from '../../../components/forms/FormInputDateTime'
import { FormSubmitButton } from '../../../components/forms/FormSubmitButton'

const StyledStatisticsFilter = styled(FlexRowSingle)`
  flex-wrap: wrap;
`

export const StatisticsPrimaryFilter = () => {
  const branches = useBranchesFilter()
  return useObserver(() => (
    <StyledStatisticsFilter justify="flex-start">
      <FlexColumn fixedWidth={true} width="12rem">
        <FormInputDateTime
          fieldName="dateTimeFrom"
          label={<Trans>From</Trans>}
          autoOk={true}
        />
      </FlexColumn>
      <FlexColumn fixedWidth={true} width="12rem">
        <FormInputDateTime
          fieldName="dateTimeTo"
          label={<Trans>To</Trans>}
          autoOk={true}
        />
      </FlexColumn>
      <FlexColumn>
        <FormSubmitButton width="15rem">
          <Plural
            value={branches.length}
            one="Show for one branch"
            few="Show for # branches"
            other="Show for # branches"
          />
        </FormSubmitButton>
      </FlexColumn>
    </StyledStatisticsFilter>
  ))
}
