import React from 'react'
import { Route, Switch } from 'react-router-dom'

import { PageTitle } from '../../../atoms/PageTitle'
import { i18n } from '../../../i18n'
import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { OverviewPage } from '../../dashboard/pages/StatsDashboardPage'
import {
  ROUTE_OVERVIEW_CLOSURE,
  ROUTE_OVERVIEW_CLOSURE_HISTORY,
  ROUTE_OVERVIEW_DASHBOARD,
  ROUTE_OVERVIEW_STATS_ORDERS,
  ROUTE_OVERVIEW_STATS_SALES,
} from '../overview.routes'
import { ClosureHistoryPage } from '../pages/ClosureHistoryPage'
import { ClosurePage } from '../pages/ClosurePage'
import { OrdersStatisticsPage } from '../pages/OrdersStatisticsPage'
import { SalesStatisticsPage } from '../pages/SalesStatisticsPage'

interface IProps {}

export const OverviewRouting: React.FC<IProps> = () => (
  <>
    <PageTitle title={i18n.t`Overview`} />
    <Switch>
      <SecureRoute
        exact
        path={ROUTE_OVERVIEW_DASHBOARD}
        component={OverviewPage}
      />
      <SecureRoute
        path={ROUTE_OVERVIEW_CLOSURE_HISTORY}
        component={ClosureHistoryPage}
      />
      <SecureRoute
        path={`${ROUTE_OVERVIEW_CLOSURE}/:reportId?`}
        component={ClosurePage}
      />
      <SecureRoute
        path={ROUTE_OVERVIEW_STATS_SALES}
        component={SalesStatisticsPage}
      />
      <SecureRoute
        path={ROUTE_OVERVIEW_STATS_ORDERS}
        component={OrdersStatisticsPage}
      />
      <Route component={NotFoundPage} />
    </Switch>
  </>
)

export default OverviewRouting
