import React from 'react'

import {
  QFilterCashiersCashiers,
  useQFilterCashiers,
} from '../../graph/generated'
import { ARRAY_EMPTY } from '../../helpers/form'
import { formatFullName } from '../../helpers/formats'
import { i18n } from '../../i18n'
import { FormDropdownMulti } from '../forms/FormDropdownMulti'
import { useBranchesFilter } from './BranchesFilter'

type TCashier = QFilterCashiersCashiers

export const CashiersFilter = React.memo<NoChildren>(() => {
  const branches = useBranchesFilter()
  const { getStash, loading } = useQFilterCashiers({
    branches,
  })
  return (
    <FormDropdownMulti<TCashier>
      fieldName="cashiers"
      isLoading={loading}
      options={getStash(data => data.cashiers) || ARRAY_EMPTY}
      placeholder={i18n.t`Filter by cashier`}
      formatOptionLabel={formatFullName}
      getOptionFilter={formatFullName}
    />
  )
})
